<template>
	<div class="music-list">
		<div class="back" @click="back">
			<i class="icon-back"></i>
		</div>
		<h1 class="title" v-html="title"></h1>
		<div class="bg-image" :style="bgStyle" ref="bgImage">
			<div class="filter"></div>
			<div class="play-wrapper" ref="playBtn">
		        <div ref="playBtn" v-show="songs.length>0" class="play">
		          <i class="icon-play"></i>
		          <span class="text">随机播放全部</span>
		        </div>
      		</div>
		</div>
		<div class="bg-layer" ref="layer"></div>
		<scroll class="scroll" :data="songs" ref="scroll" :probe-type="probeType" :listen-scroll="listenScroll" @scroll="scroll">
			<div class="song-list-wrapper">
				<song-list :song-list="songs" @select="selectItem"></song-list>
			</div>
		</scroll>
		
	</div>
</template>
<script>
import SongList from "components/song-list/song-list.vue"
import Scroll from "base/scroll.vue"
import {prefixStyle} from 'common/js/dom'
import { mapActions } from "vuex"

const transform = prefixStyle('transform')
const backdrop = prefixStyle('backdrop-filter')
	export default{
		props:{
			bgImage:{
				type:String,
				default:''
			},
			songs:{
				type:Array,
				default:[],
			},
			title:{
				type:String,
				default:''
			}
		},
		data(){
			return{
				scrollY:-1,
			}
		},
		components:{
			SongList,
			Scroll,
		},
		computed:{
			bgStyle(){
				return `background-image:url( ${ this.bgImage } )`
			}
		},
		created(){
			this.probeType=3,
			this.listenScroll=true

		},
		mounted(){
			this.$refs.scroll.$el.style.top=`${ this.$refs.bgImage.clientHeight }px`
			this.imageHeight=this.$refs.bgImage.clientHeight;
			this.minTransalteY = -this.imageHeight + 40
		},
		methods:{
			back(){
				this.$router.push( {
					path:"/singer"
				} )
			},
			scroll(pos){
				this.scrollY=pos.y;
			},
			selectItem( item,index ){
				this.selectPlay( {
					list:this.songs,
					index,
				} )
			},
			...mapActions([
				"selectPlay"
			])
		},
		watch:{
			scrollY(newY){
				let zIndex=0;
				let scale=1;
				let blur=0;
				let transtionY=Math.max( this.minTransalteY,newY )
				this.$refs.layer.style[transform]=`translate3d( 0,${ transtionY }px,0 )`;
				let per=Math.abs( newY/this.minTransalteY );
				if( newY>0 ){
					scale=1+per;
				}else{
					blur=Math.min( per*20,20 );
				}
				this.$refs.bgImage.style[backdrop]=`blur( ${ blur }px )`
				if( newY<this.minTransalteY ){
					this.$refs.bgImage.style.paddingTop=0;
					this.$refs.bgImage.style.height="40px";
					this.$refs.playBtn.style.display="none"
					zIndex=10;
				}else{
					this.$refs.bgImage.style.paddingTop="70%";
					this.$refs.bgImage.style.height=0;
					this.$refs.playBtn.style.display="block"
					zIndex=0;
				}
				this.$refs.bgImage.style.zIndex=zIndex;
				this.$refs.bgImage.style[transform]=`scale( ${ scale } )`;
			},
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/veriable"
  @import "~common/stylus/mixin"       		
  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .scroll
      position: absolute
      top: 0
      bottom: 0
      width: 100%
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
</style>


